@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensanslight.woff") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page-body {
  margin: 0;
  min-height: 100%;
  display: grid;
  align-content: start;
  grid-template-rows: min-content 1fr min-content;
  font-family: "Open Sans", sans-serif;
  position: relative;
}

.logo {
  width: 74px;
  height: 23px;
}

.logo--footer {
  width: 145px;
  height: 40px;
}

.logo__svg {
  display: block;
}

.logo__svg--white-mobile {
  display: block;
  fill: #ffffff;
}

.logo:hover .logo__svg--white-mobile {
  fill: #d22856;
}

.logo:active .logo__svg--white-mobile {
  opacity: 0.3;
  fill: #ffffff;
}

.logo__svg--white-tablet {
  display: none;
  fill: #ffffff;
}

.logo__svg--white-desktop {
  display: none;
  fill: #ffffff;
}

.logo__svg--blue-tablet {
  display: block;
  fill: #283645;
}

.logo:hover .logo__svg--blue-tablet {
  fill: #d22856;
}

.logo:active .logo__svg--blue-tablet {
  opacity: 0.3;
  fill: initial;
}

.logo__svg--blue-desktop {
  display: none;
  fill: #283645;
}

@media (min-width: 660px) {
  .logo {
    width: 145px;
    height: 40px;
  }
  .logo__svg {
    width: 145px;
    height: 40px;
  }
  .logo__svg--white-mobile {
    display: none;
  }
  .logo__svg--white-tablet {
    display: block;
  }
  .logo:hover .logo__svg--white-tablet {
    fill: #d22856;
  }
  .logo:active .logo__svg--white-tablet {
    opacity: 0.3;
    fill: #ffffff;
  }
  .logo__svg--white-desktop {
    display: none;
  }
}
@media (min-width: 1200px) {
  .logo {
    width: 147px;
    height: 40px;
  }
  .logo__svg {
    width: 147px;
    height: 40px;
  }
  .logo__svg--white-mobile {
    display: none;
  }
  .logo__svg--white-tablet {
    display: none;
  }
  .logo__svg--white-desktop {
    display: block;
  }
  .logo:hover .logo__svg--white-desktop {
    fill: #d22856;
  }
  .logo:active .logo__svg--white-desktop {
    opacity: 0.3;
    fill: initial;
  }
  .logo__svg--blue-tablet {
    display: none;
  }
  .logo__svg--blue-desktop {
    display: block;
  }
  .logo:hover .logo__svg--blue-desktop {
    fill: #d22856;
  }
  .logo:active .logo__svg--blue-desktop {
    opacity: 0.3;
    fill: initial;
  }
}
.nav-button {
  padding: 0;
  border: none;
  display: block;
  background: transparent;
}

.nav-button--sandwich {
  width: 50px;
  height: 24px;
}

.nav-button--cross {
  width: 23px;
  height: 23px;
}

.nav-button svg {
  fill: #ffffff;
}

.nav-button:hover svg {
  fill: #d22856;
}

.nav-button:active svg {
  opacity: 0.3;
}

.nav-button__icon--sandwich {
  width: 50px;
  height: 24px;
  display: none;
}

.nav-button__icon--cross {
  width: 23px;
  height: 23px;
  display: block;
}

@media (min-width: 1200px) {
  .nav-button {
    display: none;
  }
}
.main-nav {
  width: 100%;
}

.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  border-top: 1px solid #000000;
}

.main-nav__item {
  text-align: center;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  border-bottom: 1px solid #000000;
  padding-top: 14px;
  padding-bottom: 16px;
  padding-left: 20px;
  padding-right: 20px;
}

.main-nav__link {
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
}

.main-nav__link:hover {
  color: #d22856;
}

.main-nav__link:active {
  opacity: 0.3;
}

.main-nav__link--active:hover {
  color: #ffffff;
}

.main-nav__link--active:active {
  opacity: 1;
}

@media (min-width: 1200px) {
  .main-nav {
    width: auto;
  }
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    border-top: none;
  }
  .main-nav__item {
    border: none;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 16px;
    margin-right: 79px;
  }
  .main-nav__item:last-of-type {
    margin-right: 0;
  }
  .main-nav__link {
    line-height: 18px;
    font-weight: 400;
    text-transform: none;
  }
  .main-nav__link:active {
    color: #ffffff;
  }
  .main-nav__link--active {
    position: relative;
  }
  .main-nav__link--active::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: -9px;
    height: 2px;
    width: 100%;
    background: #ffffff;
  }
}
.main-header {
  background: #283645;
}

.main-header__top {
  display: flex;
  padding-top: 23px;
  padding-bottom: 23px;
  padding-left: 20px;
  padding-right: 20px;
}

.main-header__nav-button {
  margin-left: auto;
}

@media (min-width: 660px) {
  .main-header__top {
    padding-top: 27px;
    padding-bottom: 42px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .main-header__nav-button {
    margin-top: 16px;
  }
}
@media (min-width: 1200px) {
  .main-header {
    position: relative;
    z-index: 0;
    display: flex;
    justify-content: space-between;
    padding-top: 27px;
    padding-bottom: 27px;
    padding-left: 130px;
    padding-right: 130px;
    background-image: url("../img/promo-background-desktop@1x.jpg");
