.socials {
  list-style: none;
  padding: 0;
  margin-top: 0;
  margin-left: 0;
  margin-right: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 178px;
  box-sizing: border-box;
}

.socials__item {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 8px;
  margin-bottom: 8px;
}

.socials__item:nth-of-type(3n) {
  margin-right: 0;
}

.socials__link {
  width: 48px;
  height: 48px;
  border: 3px solid rgba(40, 54, 69, 0.1);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.socials__link:hover {
  border-color: #283645;
}

.socials__link:active {
  border: 3px solid rgba(40, 54, 69, 0.1);
}

.socials__link:active svg {
  opacity: 0.3;
}

@media (min-width: 660px) {
  .socials {
    max-width: 240px;
    padding-right: 7px;
