    max-width: 349px;
    padding-left: 10px;
    padding-right: 0;
  }
  .socials__item {
    margin-right: 5px;
    margin-bottom: 5px;
  }
  .socials__item:nth-of-type(3n) {
    margin-right: 5px;
  }
  .socials__item:nth-of-type(4n) {
    margin-right: 5px;
  }
  .socials__item:nth-of-type(6n) {
    margin-right: 0;
  }
}
.copyright {
  display: flex;
  align-items: center;
}

.copyright__text {
  font-size: 14px;
  line-height: 18px;
  color: #283645;
  margin-top: 2px;
  margin-right: 15px;
}

.copyright__link {
  width: 27px;
  height: 34px;
}

.copyright__link svg {
  fill: #d22856;
}

.copyright__link:hover svg {
  fill: #283645;
}

.copyright__link:active svg {
  fill: #283645;
