.main-content__special {
  display: none;
}

.main-content__wide-post {
  margin-bottom: 20px;
}

@media (min-width: 660px) {
  .main-content__special {
    display: block;
  }
  .main-content__wide-post {
    margin-bottom: 50px;
  }
}
.promo {
  padding-top: 22px;
  padding-bottom: 31px;
  padding-left: 20px;
  padding-right: 20px;
  background-repeat: no-repeat;
  background-color: #1d2631;
  position: relative;
}

.promo::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 395px;
  background-repeat: no-repeat;
  background-image: url("../img/promo-hand@1x.png"), url("../img/promo-background-mobile@1x.jpg");
  background-size: 273px auto, auto 553px;
  background-position: calc(50% - 39px) calc(100% + 28px), 50% -130px;
}
@media (min-resolution: 2dppx) {
  .promo::before {
    background-image: url("../img/promo-hand@2x.png"), url("../img/promo-background-mobile@2x.jpg");
  }
}

.promo__title {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 24px;
  line-height: 29px;
  font-weight: 300;
  color: #ffffff;
  text-align: center;
  position: relative;
  z-index: 1;
}

.promo__button {
  margin-top: 343px;
  margin-bottom: 32px;
}

.promo__platforms-list {
