    background-image: url("../img/promo-hand@1x.png"), url("../img/promo-background-desktop@1x.jpg");
    background-size: 560px auto, 2560px auto;
    background-position: calc(50% + 152px) 100%, 50% 100%;
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
