.button {
  font-family: "Open Sans", sans-serif;
  display: block;
  box-sizing: border-box;
  background: #2aab6d;
  border-radius: 3px;
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  text-align: center;
  padding-top: 15px;
  padding-bottom: 16px;
  padding-left: 10px;
  padding-right: 10px;
}

.button:hover {
  background: #1c9b5e;
}

.button:active {
  color: rgba(255, 255, 255, 0.3);
  background: #1c9b5e;
}

.button:disabled {
  background: #e5e5e5;
  color: #ffffff;
}

.button--rounded {
  line-height: 18px;
  background: transparent;
  border: 3px solid #ffffff;
  border-radius: 29px;
}

.button--rounded:hover {
  color: #d22856;
  background: #ffffff;
}

.button--rounded:active {
  color: rgba(210, 40, 86, 0.3);
}

.button--editor-submit {
  color: #d22856;
  border-color: #d22856;
  background: transparent;
}

.button--editor-submit:hover {
  color: #ffffff;
  background: #d22856;
}

.button--editor-submit:active {
  color: rgba(255, 255, 255, 0.3);
  background: #d22856;
}

.button--editor-cancel {
  color: rgba(40, 54, 69, 0.1);
  border-color: rgba(40, 54, 69, 0.1);
}

.button--editor-cancel:hover {
  color: #b5bab3;
  border-color: #b5bab3;
  background: transparent;
}

.button--editor-cancel:active {
  color: rgba(255, 255, 255, 0.3);
  background: #b5bab3;
  border-color: #b5bab3;
}

.button--form-submit {
  border: none;
  text-transform: uppercase;
  padding-top: 16px;
  padding-bottom: 17px;
}

.button--popup {
  border: none;
  padding-top: 16px;
  padding-bottom: 17px;
  text-transform: uppercase;
}

@media (min-width: 1200px) {
  .button--rounded {
    font-size: 21px;
    line-height: 21px;
    padding-top: 15px;
    padding-bottom: 14px;
  }
  .button--editor-submit {
    font-size: 18px;
  }
  .button--editor-cancel {
    font-size: 18px;
  }
}
