.input-block {
  display: flex;
  flex-direction: column;
}

.input-block__label {
  font-size: 14px;
  line-height: 30px;
  color: $red-color1;
  text-transform: uppercase;
  padding-left: 23px;
  margin-bottom: 5px;
}

.input-block__input {
  border: 2px solid $gray-color4;
  border-radius: 3px;
  padding-top: 14px;
  padding-bottom: 15px;
  padding-left: 20px;
  padding-right: 20px;
  outline: none;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  line-height: 30px;
  color: $blue-color1;
}

.input-block__input::placeholder {
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  line-height: 30px;
  color: $blue-color1;
}

.input-block__input:hover {
  border: 2px solid $blue-color1;
}

.input-block__input:active {
  border: 2px solid $red-color1;
}

.input-block__input:focus {
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.2);
}

@media (min-width: $tablet-width) {
  .input-block {
    flex-direction: row;
    align-items: center;
    max-width: 100%;
  }

  .input-block--contacts {
    flex-direction: column-reverse;
    align-items: stretch;
    position: relative;
  }

  .input-block--contacts::before {
    content: "";
    position: absolute;
    top: 2px;
    right: 2px;
    width: 59px;
    height: 59px;
    background: $gray-color4;
  }

  .input-block--contacts::after {
    content: "";
    position: absolute;
  }

